.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

body {
  background-color: rgb(42, 42, 46);
}

h1.mainHeader {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 50px;
  color: white;
}

.credit-container {
  position: absolute;
  top: 3px;
  right: 10px;
}

.header-container {
  display: grid;
  place-content: center;
  justify-content: center;
}

.subtitle-container {
  display: grid;
  place-content: center;
  justify-content: center;
  margin-bottom: 2%;
}

.input-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.submit-container {
  margin-top: 2%;
  display: flex;
  justify-content: center;
  align-items: center;
}

button.submit-btn {
  cursor: pointer;
  height: 30px;
  width: 120px;
  font-size: 120%;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: bold;
  border-radius: 10px;
}

input.email-input {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  width: 35%;
  height: 30px;
  font-size: 27px;
  border-radius: 10px;
}

.reference-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

input.reference {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  width: 15%;
  height: 30px;
  font-size: 27px;
  border-radius: 10px;
  margin-bottom: 1%;
}

input.company {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  width: 15%;
  height: 30px;
  font-size: 27px;
  border-radius: 10px;
  margin-bottom: 1%;
  margin-left: 4.5%;
}

input.subject-line {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  height: 27px;
  font-size: 25px;
  border-radius: 10px;
  width: 40%;
}

.subject-line-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.settings-container {
  padding-left: 10px;
  padding-top: 10px;
}

.template-container {
  margin-top: 2%;
  display: flex;
  justify-content: center;
  align-items: center;
}

button.settings-btn {
  cursor: pointer;
  width: 120px;
  height: 30px;
  font-size: 100%;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: bold;
  border-radius: 10px;
}

.credit-container {
  position: absolute;
  top: -9px;
  right: 10px;
}

h3.credit {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-style: italic;
  font-size: 20px;
  color: white;
}

h3.subtitle {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 25px;
  font-style: italic;
  color: white;
}

textarea.template-input {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  width: 40%;
  min-height: 400px;
  font-size: 19px;
  border-radius: 10px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
